.game-panel > * {
  padding: 10px;
}

.game-status {
  text-align: center;
}

.game-status .color-code {
  font-weight: bold;
  text-decoration: underline;
}

.color-field-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.game-status.success h3 {
  color: var(--success-color);
}

.game-status.failure h3 {
  color: var(--failure-color);
}



